


/* STRUCTURE */

.wrapper {
  display: flex;
  align-items: center;
  flex-direction: column; 
  justify-content: center;
  width: 100%;
  min-height: 100%;
  padding: 20px;
}
/* redirect to opposite register page */
.redirect-register{
  display:flex;
  justify-content: center;
  text-align: center
}
.redirect-register-text{
  color:var(--white);
  font-family: "arial";
  position: relative;
  top:-10px;
  font-size:15px;
}
.redirect-register-click{
  color:var(--titleColor);
  font-size:17px;
  margin-bottom: 20px;
}
.redirect-register-click:hover{
  color:var(--white)
}


#formContent {
  -webkit-border-radius: 10px 10px 10px 10px;
  border-radius: 10px 10px 10px 10px;
  background: var(--dark);
  padding: 30px;
  width: 90%;
  max-width: 450px;
  position: relative;
  padding: 0px;
  -webkit-box-shadow: 0 30px 60px 0 rgba(0,0,0,0.3);
  box-shadow: 0 30px 60px 0 rgba(0,0,0,0.3);
  text-align: center;
}

/* TABS */
.register-title{
  color: var(--titleColor);
  padding:20px;
}
@media screen and (max-width:520px){
  .register-title{
    font-size:30px;
  }
}

/* FORM TYPOGRAPHY*/

input[type=submit], input[type=reset]  {
  background-color: var(--background);
  border: none;
  color: var(--white);
  padding: 15px 80px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  -webkit-border-radius: 5px 5px 5px 5px;
  border-radius: 5px 5px 5px 5px;
  margin: 5px 20px 40px 20px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

input[type=submit]:hover, input[type=reset]:hover  {
  background-color: var(--background);
  color:var(--titleColor);
  -webkit-box-shadow: 0 10px 30px 0 var(--titleColor);
  box-shadow: 0 10px 30px 0 var(--titleColor);
}

input[type=submit]:active, input[type=reset]:active  {
  -moz-transform: scale(0.95);
  -webkit-transform: scale(0.95);
  -o-transform: scale(0.95);
  -ms-transform: scale(0.95);
  transform: scale(0.95);
}

input[type=password],input[type=text] {
  background-color: var(--dark);
  border: none;
  color: #0d0d0d;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 5px;
  width: 86%;
  border: 2px solid var(--background);
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-border-radius: 5px 5px 5px 5px;
  border-radius: 5px 5px 5px 5px;
  color:var(--titleColor);
}

input[type=password]:focus,input[type=text]:focus {
  background-color: var(--dark);
  border-bottom: 2px solid var(--titleColor);
  color:var(--titleColor);
}

input[type=password]:placeholder,input[type=password]:placeholder {
  color: #cccccc;
}
@media screen and (max-width:470px) {
  input[type=submit]{
    font-size:10px;
    margin-right:auto;
    margin-left:auto;
    text-align: center;
    padding: 15px 50px;


  }
}


/* ANIMATIONS */

/* Simple CSS3 Fade-in-down Animation */
.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
/*FADE IN*/
@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}



/* OTHERS */

*:focus {
    outline: none;
} 

#icon {
  width:60%;
}